const ASSETS = import.meta.glob(
  '/src/assets/**/*.{png,jpg,jpeg,svg,gif,webp,mp4}',
  { eager: true, query: '?url', import: 'default' }
) as Record<string, string>;

const assetUrl = (p: string) => {
  // existing strings in your repo are like 'src/assets/...'
  const key = p.startsWith('/src/') ? p : `/${p}`;
  const url = ASSETS[key];
  if (!url) {
    console.warn(`[assetUrl] missing asset for ${key}`);
    return p; // fallback to original string so dev still works (but will 404 in prod)
  }
  return url;
};

